<template>
  <b-container id='app'>
    <b-row>
      <b-col sm="12" md="8" lg="8">
        <div class="square">
          <ul v-for="(obj, index) in testImg" style="padding: 0;">
            <li :id="index">
              <h1><b># {{index + 1}}</b></h1>
              <div @click="changeToFullScreen(obj)">
                <b-img v-bind:src="obj" fluid></b-img>
              </div>
              <div style="position: fixed; background-color:rgba(0,0,0,0.25); max-height: 100%; max-width: 100%; height: 100%; width: 100%; top: 50%; left: 50%; z-index: 9900;
              transform: translate(-50%, -50%)" v-if="fScreen === true" class="d-none d-md-block">
                <h1 class="text-white" style="position: absolute; top: 5%; right: 5%;" @click="closeFullScreen">X</h1>
                <div @click="changeToLeft" style="height: 1.5rem; width: 1.5rem; border: .2rem ghostwhite solid; border-top: transparent; border-right: transparent; transform: rotate(45deg); position: absolute; top: 50%; left: 3%; z-index: 9991;"></div>
                <div @click="changeToRight" style="height: 1.5rem; width: 1.5rem; border: .2rem ghostwhite solid; border-left: transparent; border-bottom: transparent; transform: rotate(45deg); position: absolute; top: 50%; right: 3%; z-index: 9991;"></div>
                <div style="position: absolute; width: 100% ;height: 80%; top: 50%; transform: translateY(-50%)">
                 <b-img v-bind:src="fScreenImg" v-if="fScreenImg !== undefined" fluid style="width: 80%; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); max-height: inherit; max-width: inherit;"></b-img>
                  <h1 v-if="fScreenImg === undefined" class="text-white" style="position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);">沒有圖片可以看ㄛ</h1>
                </div>
              </div>
              <p>{{now}}</p>
            </li>
          </ul>
        </div>
      </b-col>
      <b-col sm="0" md="4" lg="4" class="d-none d-md-block">
        <div class="square" style="position: fixed;">
          <div style="position: relative; max-width: 20rem;">
            <h1>{{msg}}</h1>
            <a href="#1">lorem</a><br>
            <a href="#2">lorem</a><br>
            <a href="#3">lorem</a><br>
            <a href="#4">lorem</a><br>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
          </div>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
  Date.prototype.myDate = function () {
    if (this.getMinutes().length < 2) {
      let minute = JSON.stringify(this.getMinutes()).split("")
      this.myProp = minute.unshift("0")
    } else {
      this.myProp = this.getMinutes()
    }
  }
  let date = new Date()
  date.myDate()
  let minute = date.myProp
  export default {
    name: 'HelloWorld',
    data() {
      return {
        now: `${date.getFullYear()}/${date.getMonth()}/${date.getDate()} ${date.getHours()}: ${minute}`,
        msg: 'Welcome to Your Vue.js App',
        text: `
        Quis magna Lorem anim amet ipsum do mollit sit cillum voluptate ex nulla
        tempor. Laborum consequat non elit enim exercitation cillum aliqua
        consequat id aliqua. Esse ex consectetur mollit voluptate est in duis
        laboris ad.
      `,
        h1Text: `
      Quis magna.
      `,
        testImg: ['https://i.imgur.com/efFTTIT.jpg', 'https://i.imgur.com/AM3lVYo.gif', 'https://i.imgur.com/BwXxYmu.jpg', 'https://i.imgur.com/tZL0CbA.gif'
          , 'https://i.imgur.com/309wjS9.jpg', 'https://i.imgur.com/GynjwN2.jpg', 'https://i.imgur.com/oo4TKze.jpg'],
        fScreen: false,
        fScreenImg: '',
        innerHeight: window.innerHeight,
        innerWidth: window.innerWidth,
        date: date.myProp
      }
    },
    computed: {
      fullScreenLeft() {
        if (this.fScreenImg !== '') {
          return this.testImg[this.testImg.indexOf(this.fScreenImg) - 1]
        }
      },
      fullScreenRight() {
        if (this.fScreenImg !== '') {
          return this.testImg[this.testImg.indexOf(this.fScreenImg) + 1]
        }
      }
    },
      methods: {
        changeToFullScreen(img) {
          this.fScreenImg = img
          this.fScreen = !this.fScreen
        },
        closeFullScreen() {
          this.fScreenImg = ''
          this.fScreen = !this.fScreen
        },
        changeToLeft() {
          this.fScreenImg = this.fullScreenLeft
        },
        changeToRight() {
          this.fScreenImg =  this.fullScreenRight
        }
      }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



